body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #11A9BA;
  --main-color-clear:#11A9BA70;
  --second-color: #BA2211;
  --second-color-clear:#BA221170;
  --third-color: #6E2F00;
  --third-color-clear:#6E2F0070;
  --dark-color: #05636E;
  --dark-color-clear: #05636E70;
  --link-color: #30EAFF;
  --light-color: white;
}

html, body {
  max-width: 100%;
  /* overflow-x: hidden; */
}

/* Modify default fonts */
p {
  font-family: "Quicksand";
  font-size: 16pt;
  margin: 8px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Exo";
}

/* Custom stuff */

.pagenav {
  background-color: darkcyan;
  border-radius: 20px;
}

.btn-light {
  background-color: var(--second-color);
  color: white;
}

.btn-light:hover {
  background-color: var(--third-color);
  color: white;
}

.nav-text {
  color: white;
  font-family: "Quicksand";
}

.title {
  padding: 12px;
  font-size: 62pt;
  font-family: "Exo";
  font-weight: 1000;
  text-transform: uppercase;
  color: var(--second-color);
}

.subtitle {
  padding: 8px;
  font-size: 16pt;
  font-family: "Exo";
  font-weight: 900;
  color: var(--third-color);
}

.landing-segment {
  height: 400px;
  padding: 12px;
  background-color: wheat;
  font-size: 30pt;
  font-family: "Exo";
  font-weight: 1000;
  text-transform: uppercase;
}

.title-segment {
  text-align: center;
  font-size: 54pt;
  font-family: "Exo";
  font-weight: 200;
  text-transform: uppercase;
  color: var(--second-color);
}

.content-container {
  width: 75%
}

.content-segment {
}

.content-segment > img {
  display: block;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 26px;
}

.content-segment > p {
  /* display: flex; */
  /* align-items: center; */
  margin-top: 36px;
  margin-bottom: 36px;
}

.divider {
  position: relative;
  left: -50%;
  height: 160px;
  width: 300%;
  margin-top: 68px;
  margin-bottom: 68px;
  background-color: var(--main-color)
}

.divider-flat {
  position: relative;
  left: -50%;
  height: 160px;
  width: 300%;
  background-color: var(--main-color)
}

.segment-footer {
  color: white;
  height: 80px;
  margin-top: 20px;
  padding: 8px;
}

.segment-dark-sm, .segment-light-sm {
  min-height: 250px;
  padding: 26px;
}

.segment-dark, .segment-light {
  min-height: 400px;
  padding: 18px;
}

.segment-dark, .segment-dark-sm, .segment-footer {
  background-color: var(--main-color);
}

.segment-light, .segment-light-sm {
  background-color: var(--white-color);
}

.showcase-box {
  font-style: normal;
  text-decoration: none;
  padding: 8px;
  border-radius: 10px;
  /* So that it defaults to black if it is an <a> */
  color: black;
}

.showcase-box > * {
  user-select: none;
  pointer-events: none;
}

.showcase-box:hover {
  background-color: var(--second-color);
  transition: background-color 0.2s;
  transform: scale(1.01);
}

.showcase-box:hover * {
  color: white !important;
  transition: color 0.2s;
  transform: scale(1);
}

.img-showcase {
  /* border-radius: 16px; */
  width: 400px;
}
